import React, { Component } from "react";
import {Card, CardBody, CardImg, CardText, CardTitle, Media} from "reactstrap";
import {Link} from "react-router-dom";
import {COMMENTS} from "../../shared/comments";
import {PROMOTIONS} from "../../shared/promotions";
import {LEADERS} from "../../shared/leaders";
import {ACCOUNTS} from "../../shared/accounts";





class Order extends Component{
    constructor(props) {
        super(props);
        this.state = {
            orders: this.props.orders,
            books: this.props.books
        };
        this.setState({orders:this.props.orders});
        this.RenderOrder = this.RenderOrder.bind(this);
        if (sessionStorage.getItem("user_id") !== null) {
            fetch('http://localhost:9090/order/' + String(sessionStorage.getItem("user_id")),{
                method:'GET'
            })
                .then(res =>res.json())
                .then((data) => {
                    console.log(data.status);
                    if (data.status === undefined) {
                        this.setState({orders:data});
                    }

                });
        }

    }

    RenderOrder() {
        if (this.state.orders == null || this.state.orders.length === 0) {
            return (
                <div key = "order_div" className="col-12 col-md-12">
                    <Card>
                        <h1>暂无订单</h1>
                    </Card>
                </div>
            );
        }
        console.log("orders", this.state.orders);
        if (this.state.orders === null || this.state.orders === undefined) {
            return (
                <div></div>
            );
        }
        let renderedOrders = this.state.orders.map((order, index) => {
            let renderedItems = order.orderItemSet.map((item, index) => {
                let item_id = item.book.id;
                let book = this.state.books.filter((book) => (book.id === item_id))[0];
                // console.log(item.id);
                // console.log(this.state.books);
                if (book === undefined)
                    return (
                        <div></div>
                    );

                return (
                    <Card>
                        <Media>
                            <div className="col-12 col-md-2">
                                <CardImg width="100%" object src={book.image} alt={book.name}/>
                            </div>
                            <div className="col-12 col-md-7 m-2">
                                <Media body>
                                    <Media heading>{book.name}</Media>
                                    <Media heading>单价：{item.itemprice} 数量：{item.itemcnt}</Media>
                                    <Media heading>优惠金额：{item.discountmoney}</Media>
                                    <Media heading>实付金额：{item.totalmoney}</Media>
                                </Media>
                            </div>
                        </Media>
                    </Card>

                );
            });
            console.log("order_items = ", renderedItems);
            let date = new Date(order.createtime);
            // 格式化日期
            let dateTime = date.toLocaleString();
            // let ans = await fetch('http://localhost:9090/order_item/' + String(order.orderid), {method:'GET'}).data;
            // console.log(ans);
            return (
                <div key = {index} className="col-12 col-md-12">
                    <Card>
                        <Media>
                            <div className="col-12 col-md-7 m-2">
                                <Media body>
                                    <Media heading>订单编号：{order.orderid}   订单金额：¥{order.payment}</Media>
                                    <Media heading>下单时间：{dateTime}</Media>
                                </Media>
                                {renderedItems}
                            </div>
                        </Media>
                    </Card>
                </div>
            );
        });

        return (
            <React.Fragment>
                {renderedOrders}
            </React.Fragment>
        );
    }

    render() {
        let orders = this.RenderOrder();
        return (
            <React.Fragment>
                <div className="container">
                    {orders}
                </div>
            </React.Fragment>
        );
    }


}

export default Order;
