import React, { Component } from "react";
import {Card, CardBody, CardImg, CardText, CardTitle, Media} from "reactstrap";
import {Link} from "react-router-dom";

function RenderBookInCar({books, car, clearCar}) {
    function handleSubmit(event) {
        event.preventDefault();
        let result_string = JSON.stringify(car);
        let searchParams = new URLSearchParams();
        searchParams.append("cart_list", result_string);
        searchParams.append("user_id", sessionStorage.getItem("user_id"));
        fetch('http://localhost:9090/purchase/all',{
            method:'POST',
            body:searchParams
        })
            .then(res =>res.json())
            .then((data) => {
                console.log(data);
                car = data;
                alert("购买成功！");
                clearCar();
            })
    }

    function inCart(book, car) {
        let len = car.length;
        for (let i = 0; i < len; i++) {
            if (book.id === car[i].id) return true;
        }
        return false;
    }
    let book_filtered = books.filter((book) => inCart(book, car));
    let renderedBooks = book_filtered.map((book) => {
        let len = car.length;
        let car_item;
        for (let i = 0; i < len; i++) {
            if (book.id === car[i].id) car_item = car[i];
        }
        return (
            <div key = "bookimage" className="col-12 col-md-12">
                <Card>
                    <Media>
                        <div className="col-12 col-md-2">
                            <CardImg width="100%" object src={book.image} alt={book.name}/>
                        </div>
                        <div className="col-12 col-md-7 m-2">
                            <Media body>
                                <Media heading>{book.name}</Media>
                                <Media><Media heading>¥{book.price * car_item.number}</Media></Media>
                                <Media><Media heading>购买数量:{car_item.number}</Media></Media>
                            </Media>
                        </div>
                    </Media>
                </Card>
            </div>
        );
    });
    if (car.length === 0) return (
        <div key = "bookimage" className="col-12 col-md-12">
            <Card>
                <h1>当前购物车为空</h1>
            </Card>
        </div>
    );
    else return (
        <React.Fragment>
            {renderedBooks}
            <div key = "bookdescription" className="col-12 col-md-2 m-1">
                <Link to={`/car/`} onClick={handleSubmit}>
                    <CardImg  object src="/assets/images/buy.png" alt="立即购买" />
                </Link>
            </div>

        </React.Fragment>
    );

}



class Car extends Component{
    constructor(props) {
        super(props);
    }

    render() {

        let car = this.props.car;
        let totalPrice = 0;
        let totalBooks = 0;
        for(let i = 0, len=car.length; i < len; i++) {
            totalPrice += parseFloat(car[i].price) * car[i].number;
            totalBooks += car[i].number;
        }
        totalPrice = totalPrice.toFixed(2);
        return (
            <React.Fragment>
                <div className="container">
                    <RenderBookInCar books = {this.props.books} car={car} clearCar = {this.props.clearCar} />
                    <div key = "bookTotalPrice" className="col-12 col-md-12">
                        <Card>
                            <CardTitle>共{totalBooks}本 总价为{totalPrice} </CardTitle>
                        </Card>
                    </div>
                </div>
            </React.Fragment>
        );
    }


}

export default Car;
